*,
*:after,
*:before {
  box-sizing: border-box;
  margin: 0;
  padding: 0; }

input:focus {
  outline: none; }

/* Remove default padding */
ul,
ol {
  padding: 0; }

input::-ms-clear {
  display: none; }

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  display: none;
  -webkit-appearance: none;
  appearance: none;
  margin: 0; }

input[type='number'] {
  -moz-appearance: textfield; }

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0; }

/* Set core body defaults */
html {
  scroll-behavior: smooth; }

body {
  min-height: 100vh;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
  overflow-x: hidden; }

a {
  color: inherit;
  text-decoration: none; }

img {
  max-width: 100%; }

/* Remove list styles on ul, ol elements with a class attribute */
ul,
ol {
  list-style: none; }

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto; }

/* Make images easier to work with */
img {
  display: block; }

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit; }

a {
  text-decoration: none;
  color: initial; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

button {
  border: none;
  background: none; }

*:focus {
  outline: none; }

@custom-media --laptop-min (min-width: 1401px);
@custom-media --laptop-max (max-width: 1400px);
@custom-media --laptop-s-min (min-width: 1281px);
@custom-media --laptop-s-max (max-width: 1280px);
@custom-media --tablet-min (min-width: 1025px);
@custom-media --tablet-max (max-width: 1024px);
@custom-media --mobile-landscape-big-min (min-width: 851px);
@custom-media --mobile-landscape-big-max (max-width: 850px);
@custom-media --mobile-landscape-min (min-width: 769px);
@custom-media --mobile-landscape-max (max-width: 768px);
@custom-media --mobile-min (min-width: 501px);
@custom-media --mobile-max (max-width: 500px);
:root {
  font-size: 14px;
  --sl-color-primary-hue: 39;
  --sl-color-primary-saturation: 94%; }
  @media (--mobile-landscape-max) {
    :root {
      font-size: 13px; } }

*[data-show='true'] {
  display: block !important; }

*[data-show='false'] {
  display: none !important; }

.wrapper {
  width: 100%;
  max-width: calc(1170px + 30px);
  margin-right: auto;
  margin-left: auto;
  padding-right: 15px;
  padding-left: 15px; }

.arrow-up-svg,
.arrow-down-svg {
